.doneTodo{
	color: #78CFB0;
	text-decoration: line-through;
	width: 432px;
    min-height: 75px;
    background-color: #15101C;
    display: flex;
    justify-content: space-between;
    padding: 28px 21px;
    border-radius: 10px;
}

.doneTodo:not(:last-child){
	margin-bottom: 16px;
}